.client {
}
.container {
}
.client__header {
  margin-top: 40px;
  text-align: center;
}
.client__header-text {
}
.client__text-slogan {
  color: #552768;
  font-family: Inter Tight, sans-serif;
  font-size: 18px;
}
.client__text-title {
  color: #552768;
  margin: 0;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 38px;
  line-height: 48px;
}
@media screen and (min-width: 1200px) {
  .client__list {
    display: flex;
    margin-bottom: 40px;
    position: relative;
    right: 0;
    animation-name: animation;
    animation-duration: 5s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
    img {
      width: 200px;
      height: 100px;
    }
    @keyframes animation {
      0% {
        right: 0;
      }
      100% {
        right: 200px;
      }
    }
  }
}
